@import url(https://fonts.googleapis.com/css?family=Rubik:300,300i,400,400i,500,500i,700,700i,900&display=swap);
:root {
  --border-radius: 4px; }

* {
  box-sizing: border-box; }

body {
  margin: 0;
  font-family: "Rubik", sans-serif;
  color: #323223; }

button {
  cursor: pointer; }

button,
input {
  outline: none;
  border: none;
  font-size: 15px; }

ul {
  list-style: none;
  padding: 0;
  margin: 0; }

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-weight: 400; }

a {
  color: inherit;
  text-decoration: none;
  /* no underline */ }

.container {
  max-width: 1100px;
  margin: 0 auto; }

.content {
  padding: 15px; }

.flex {
  display: flex; }

.column {
  flex-direction: column; }

.floating-card {
  background-color: #fff;
  border-radius: 2px;
  height: 200px; }

.responsive-grid {
  display: grid;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  row-gap: 10px;
  margin: 10px 0;
  grid-template-columns: 1fr 1fr 1fr 1fr 1fr; }
  @media (max-width: 1000px) {
    .responsive-grid {
      grid-template-columns: 1fr 1fr 1fr 1fr; } }
  @media (max-width: 670px) {
    .responsive-grid {
      grid-template-columns: 1fr 1fr; } }

.App {
  min-height: 100vh;
  background-color: #e4f3ff; }
  .App.dark {
    background-color: #000; }

.app-header-cmp {
  color: #fff;
  padding: 0 10px;
  width: 105%;
  height: 90px;
  left: -3%;
  position: relative;
  background-color: #06bcfb;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#06bcfb), color-stop(74%, #4885eebd));
  background-image: linear-gradient(0deg, #06bcfb 0%, #4885eebd 74%);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-bottom-left-radius: 50% 40%;
  border-bottom-right-radius: 50% 40%; }
  .app-header-cmp .main {
    height: 75px;
    padding: 0 20px;
    display: flex;
    justify-content: space-between;
    align-items: center; }

.nav-bar-cmp a {
  margin-left: 11px;
  font-size: 19px;
  padding-bottom: 3px; }
  .nav-bar-cmp a.active {
    border-bottom: 2px solid; }

.toggle-unit-cmp > * {
  opacity: 0.6; }

.toggle-unit-cmp button {
  background-color: unset;
  color: #fff;
  font-size: 19px; }
  .toggle-unit-cmp button.active {
    opacity: 1; }

.home-page .location-search-cmp {
  margin-top: 20px;
  margin-bottom: 30px; }

.location-search-cmp {
  position: relative; }
  .location-search-cmp input {
    width: 100%;
    padding: 15px;
    border-radius: 9px; }
  .location-search-cmp .suggests-list-cmp {
    position: absolute;
    width: 100%;
    top: 120%; }

.suggests-list-cmp {
  background-color: #fff;
  box-shadow: 0 0 4px 1px #c4c4c4;
  max-height: 300px;
  overflow-y: scroll;
  border-bottom-left-radius: 9px;
  border-bottom-right-radius: 9px; }
  .suggests-list-cmp .suggest-preview {
    cursor: pointer;
    border-bottom: 1px solid #eee;
    font-size: 15px;
    padding: 1rem; }
    .suggests-list-cmp .suggest-preview:hover {
      background-color: #eee; }

.location-details-cmp .top {
  display: flex;
  justify-content: space-between;
  background-color: #06bcfb;
  background-image: -webkit-gradient(linear, left bottom, left top, from(#06bcfb), color-stop(74%, #4885eebd));
  background-image: linear-gradient(0deg, #06bcfb 0%, #4885eebd 74%);
  color: #fff;
  margin-bottom: 10px;
  height: 220px; }
  .location-details-cmp .top > * {
    width: 33%; }
  .location-details-cmp .top .name-temperature-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between; }
    .location-details-cmp .top .name-temperature-container .temperature {
      padding-left: 5px; }
  .location-details-cmp .top .weather-icon-text {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 28px; }
    .location-details-cmp .top .weather-icon-text img {
      margin-bottom: 10px;
      width: 70px;
      height: 70px; }
  .location-details-cmp .top .toggle-heart-cmp {
    text-align: right; }

.location-details-cmp .test-1 {
  display: grid;
  grid-column-gap: 10px;
  -webkit-column-gap: 10px;
     -moz-column-gap: 10px;
          column-gap: 10px;
  grid-row-gap: 10px;
  row-gap: 10px;
  grid-template-columns: 3fr 2fr; }
  @media (max-width: 670px) {
    .location-details-cmp .test-1 {
      grid-template-columns: 1fr; } }

.location-details-cmp .location-time-container {
  font-size: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background-color: lightblue;
  background-size: 100% 100%; }
  .location-details-cmp .location-time-container h3 {
    font-weight: 300; }

@-webkit-keyframes windy {
  0% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1); }
  50% {
    -webkit-transform: scaleX(0.9);
            transform: scaleX(0.9); }
  100% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1); } }

@keyframes windy {
  0% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1); }
  50% {
    -webkit-transform: scaleX(0.9);
            transform: scaleX(0.9); }
  100% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1); } }

.location-details-cmp .wind {
  position: relative;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center; }
  .location-details-cmp .wind img {
    -webkit-animation: windy 3s infinite;
            animation: windy 3s infinite;
    position: absolute;
    opacity: 0.5;
    height: 71%;
    left: 3%;
    bottom: 9%;
    -webkit-transform-origin: left;
            transform-origin: left; }


.day-forecast-cmp {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center; }
  .day-forecast-cmp .day {
    border-bottom: 1px solid #ccc; }
  .day-forecast-cmp .icon-temperature-container {
    display: flex;
    flex-direction: column;
    align-items: center; }
    .day-forecast-cmp .icon-temperature-container img {
      width: 33px;
      margin-bottom: 15px; }

.toggle-heart-cmp {
  cursor: pointer;
  font-size: 24px;
  color: #ccc; }
  .toggle-heart-cmp.active {
    color: tomato; }
  .toggle-heart-cmp:hover {
    color: rgba(255, 99, 71, 0.678); }




.location-preview-cmp {
  cursor: pointer;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center; }
  .location-preview-cmp:hover {
    background-color: #fff; }
  .location-preview-cmp .icon-temperature-container {
    display: flex;
    flex-direction: column;
    align-items: center; }
    .location-preview-cmp .icon-temperature-container img {
      width: 33px;
      margin-bottom: 15px; }

